@charset "UTF-8";
@import "~bootstrap/dist/css/bootstrap.min.css";
@import "~@fortawesome/fontawesome-free/css/fontawesome.min.css";
@import "~@fortawesome/fontawesome-free/css/brands.min.css";
@import "~@fortawesome/fontawesome-free/css/solid.min.css";
/*------------------------------------------------------------
	Top page
------------------------------------------------------------*/
.container-fluid {
  background: url("https://wallpaperaccess.com/full/42614.jpg");
  background-position: center;
  width: 100vw;
  min-height: 100vh;
  &>div {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
#quote-box {
  padding: 20px;
}
.quote {
  font-size: 24px;
}
.author {
  font-size: 18px;
  margin-bottom: 30px;
}
.buttons {
  margin-top: 20px;
}
.left .btn {
  width: 40px;
  height: 40px;
  font-size: 20px;
  margin-right: 10px;
  padding: 5px;
}
.fa-twitter {
  font-size: 22px;
}
#new-quote {
  font-size: 16px;
  font-weight: bold;
}
.copy-right {
  color: #fff;
  font-size: 14px;
  margin-top: 10px;
}